import store from '../../redux/store';

interface User {
  name: string;
  surname: string;
  email: string;
  imgUrl: string;
}

function hideWinPopupAndHisCover(popup: HTMLElement, popupCover: HTMLElement): void {
  document.body.classList.remove('notScrollable');
  popup.classList.remove('appear');
  popupCover.classList.remove('appear');
}

function clearInputs(nameField: HTMLInputElement, surnameField: HTMLInputElement, emailField: HTMLInputElement): void {
  nameField.value = '';
  surnameField.value = '';
  emailField.value = '';
}

function clearInputsRectangle(): void {
  const allInputsRectangles = document.querySelectorAll('.popup__stroke-rect');
  allInputsRectangles.forEach((inputRectangle) => {
    inputRectangle.classList.remove('popup__stroke-rect_active');
  });
}

function validate(field: HTMLInputElement) {
  const value: string = field.value || '';
  const rectOfValidation: Element = field.closest('.popup__stroke-form').nextElementSibling.children[0];
  if (
    (field.type === 'email' && value.trim() && value.length <= 30 && field.validity.valid) ||
    (field.type !== 'email' &&
      value.trim() &&
      value.length <= 30 &&
      field.validity.valid &&
      value.split('').every((symbol) => /^\p{L}$/iu.test(symbol)))
  ) {
    rectOfValidation.classList.add('popup__stroke-rect_active');
  } else {
    rectOfValidation.classList.remove('popup__stroke-rect_active');
  }
}

function addUser(nameField: HTMLInputElement, surnameField: HTMLInputElement, emailField: HTMLInputElement) {
  const imgOfUser: HTMLImageElement | null = document.querySelector('.popup__avatar-pic');
  const user: User = {
    name: nameField.value,
    surname: surnameField.value,
    email: emailField.value,
    imgUrl: imgOfUser ? imgOfUser.src : '',
  };
  store.addUserToDataBase(user);
  store.state.newUser = user;
  store.state.isUserRegistered = true;
  store.callSubscriber();
}

const PopupLogic = (): void => {
  const buttonAddingUser: HTMLElement = document.querySelector('.popup__btn-add');
  const buttonCancelForm: HTMLElement = document.querySelector('.popup__btn-cancel');
  const cover: HTMLElement = document.querySelector('.rules__cover');
  const formOfRegistration: HTMLElement = document.querySelector('.popup');
  const nameField: HTMLInputElement = document.querySelector('.popup__name');
  const surnameField: HTMLInputElement = document.querySelector('.popup__surname');
  const emailField: HTMLInputElement = document.querySelector('.popup__email');

  cover.addEventListener('click', (): void => {
    hideWinPopupAndHisCover(formOfRegistration, cover);
  });

  buttonAddingUser.addEventListener('click', (): boolean => {
    let isInputsValid: boolean;
    if (nameField.validity.valid && surnameField.validity.valid && emailField.validity.valid) {
      addUser(nameField, surnameField, emailField);
      hideWinPopupAndHisCover(formOfRegistration, cover);
      clearInputs(nameField, surnameField, emailField);
      clearInputsRectangle();
      isInputsValid = true;
    } else {
      alert('Not at all');
      isInputsValid = false;
    }
    return isInputsValid;
  });

  buttonCancelForm.addEventListener('click', () => {
    hideWinPopupAndHisCover(formOfRegistration, cover);
    clearInputs(nameField, surnameField, emailField);
    clearInputsRectangle();
  });

  nameField.addEventListener('input', (event) => {
    validate(event.target);
  });
  surnameField.addEventListener('input', (event) => {
    validate(event.target);
  });
  emailField.addEventListener('input', (event) => {
    validate(event.target);
  });
};

export default PopupLogic;
