import Rules from '../Rules/Rules';
import Game from '../Game/Game';
import ScoreTable from '../ScoreTable/ScoreTable';
import Settings from '../Settings/Settings';

interface Route {
  path: string;
  component: Function;
}

interface Props {
  state: State;
  path: string
}

interface State {
  isUserRegistered: boolean,
  isGameStarted: boolean,
  newUser: object,
  gamePage: GamePage,
}

interface GamePage {
  timeInSeconds: number,
  numberOfAllComparisons: number,
  numberOfWrongComparisons: number,
  playerPoints: number,
  difficult: string,
  cardsType: string,
}

const Router = (props: Props) => {
  const routes: Route[] = [
    {
      path: '/',
      component: Rules,
    },
    {
      path: '/score',
      component: ScoreTable,
    },
    {
      path: '/settings',
      component: Settings,
    },
    {
      path: '/game',
      component: Game,
    },
  ];
  const { path } = props;
  const findComponentByPath = (path: string, routes: Route[]) =>
    routes.find((route: Route) => route.path.match(new RegExp(`^\\${path}$`, 'gm'))) || undefined;
  const { component } = findComponentByPath(path, routes);
  return component({ state: props.state });
};

export default Router;
