import Header from './components/Header/Header';
import Popup from './components/Popup/Popup';
import Router from './components/Router/Router';

interface Props {
  state: State;
  path: string
}
interface State {
  isUserRegistered: boolean,
  isGameStarted: boolean,
  newUser: object,
  gamePage: GamePage,
}

interface GamePage {
  timeInSeconds: number,
  numberOfAllComparisons: number,
  numberOfWrongComparisons: number,
  playerPoints: number,
  difficult: string,
  cardsType: string,
}

const App = (props: Props): string => `
  ${Header({ state: props.state })}
  <main>
  ${Popup()}
    ${Router({ path: props.path, state: props.state })}
  </main>
  `;
export default App;
