import './ScorePlayer.scss';

const ScorePlayer = (props): string => `
    <li class="score__player">
      <img
        src="${props.user.imgUrl}"
        alt="${props.user.name}"
        class="score__player-pic"
      />
      <div class="score__player-info">
        <div class="score__player-name">${props.user.name} ${props.user.surname}</div>
        <div class="score__player-email">${props.user.email}</div>
      </div>
      <div class="score__player-result">
        Score: <span class="score__player-count">${props.user.score}</span>
      </div>
    </li>
  `;
export default ScorePlayer;
