import Navbar from './Navbar/Navbar';
import './Header.scss';
import userImg from '../../assets/images/avatar.png';

interface Props {
  state: State;
}

interface State {
  isUserRegistered: boolean,
  isGameStarted: boolean,
}
const Header = (props: Props): string => {
  let headerButtonClass: string;
  let headerButtonText: string;
  let blockWithUserImg: string;
  if (props.state.isUserRegistered) {
    headerButtonClass = 'header__btn_start';
    headerButtonText = 'Start game';
    blockWithUserImg = `<img src="${userImg}" class="header__img-avatar"/>`;
  } else {
    headerButtonClass = 'header__btn_register';
    headerButtonText = 'register new player';
    blockWithUserImg = '';
  }

  if (props.state.isGameStarted) {
    headerButtonClass = 'header__btn_stop';
    headerButtonText = 'Stop game';
    blockWithUserImg = `<img src="${userImg}" class="header__img-avatar"/>`;
  }
  return `
    <header class="header">
      <div class="header__container">
        <div class="header__logo">
          <div class="header__logo-part">match</div>
          <div class="header__logo-part">match</div>
        </div>
        ${Navbar()}
        <div class="header__btn-and-user-icon">
          <button class="header__btn ${headerButtonClass}">
          ${headerButtonText}
          </button>
          ${blockWithUserImg}
        </div>
      </div>
    </header>
  `;
};
export default Header;
