import store from '../../../redux/store';

const DifficultSettingsLogic = (): void => {
  const difficultSettings: HTMLElement = document.querySelector('.difficult');
  const difficultSettingsBlockList: HTMLUListElement = difficultSettings.querySelector('.settings__types');
  const difficultSettingsTypes = difficultSettings.querySelectorAll('.settings__type');
  difficultSettings.addEventListener('click', () => {
    difficultSettingsBlockList.classList.add('appear');
  });
  difficultSettingsTypes.forEach((type) => {
    type.addEventListener('click', () => {
      if (type.classList.contains('settings__type_beginner')) {
        store.state.gamePage.difficult = 'beginner';
      } else if (type.classList.contains('settings__type_medium')) {
        store.state.gamePage.difficult = 'medium';
      }
    });
  });
};

export default DifficultSettingsLogic;
