import ScorePlayer from './ScorePlayer/ScorePlayer';
import './ScoreTable.scss';

const ScoreTable = (props): string => {
  const player: string = props.state.newUser.score ? ScorePlayer({ user: props.state.newUser }) : '';
  return `
      <div class="container">
        <section class="score">
          <h2 class="score__title">Best players</h2>
          <ul class="score__players">
            ${player}
          </ul>
        </section>
      </div>
  `;
};
export default ScoreTable;
