import './Parameter.scss';
interface Props {
  type: string,
  name: string,
  hint: string,
}

const Parameter = (props: Props): string => {
  let firstTypeClass: string;
  let secondTypeClass: string;
  let firstTypeText: string;
  let secondTypeText: string;
  if (props.type === 'cardsType') {
    firstTypeClass = 'settings__type_animals';
    secondTypeClass = 'settings__type_programming';
    firstTypeText = 'Animals';
    secondTypeText = 'Programming';
  } else if (props.type === 'difficult') {
    firstTypeClass = 'settings__type_beginner';
    secondTypeClass = 'settings__type_medium';
    firstTypeText = 'Beginner';
    secondTypeText = 'Medium';
  }
  return `
    <div class="settings__item ${props.type}">
      <div class="settings__item-container">
        <div class="setting__name">${props.name}</div>
        <div class="setting__hint">${props.hint}</div>
      </div>
      <ul class="settings__types">
        <li class="settings__type ${firstTypeClass}">${firstTypeText}</li>
        <li class="settings__type ${secondTypeClass}">${secondTypeText}</li>
      </ul>
    </div>
  `;
};
export default Parameter;
