import Card from './Card/Card';
import './Game.scss';

import Timer from './Timer/Timer';
import WinPopup from './WinPopup/WinPopup';
interface Props {
  state: State;
}

interface State {
  gamePage: GamePage,
}

interface GamePage {
  difficult: string;
}
const Games = (props: Props): string => {
  const gameDifficult: string = props.state.gamePage.difficult;
  const cardsCount: number = gameDifficult === 'beginner' ? 16 : 36;
  let cards = '';
  for (let i = 0; i < cardsCount; i++) {
    cards += Card({id: i});
  }
  return `
    <div class="container">
      <section class="game">
        ${Timer()}
        <div class="game__cards-container">
          <ul class="game__cards">
          ${cards}
          </ul>
          <div class="game__cards-cover"></div>
        </div>
        <div class="game__win-popup-cover"></div>
        ${WinPopup()}
      </section>
    </div>
  `;
};
export default Games;
