import store from '../../redux/store';

function showWinPopupAndHisCover(popup: HTMLElement, popupCover: HTMLElement): void {
  document.body.classList.add('notScrollable');
  popup.classList.add('appear');
  popupCover.classList.add('appear');
}

const HeaderLogic = (): void => {
  const buttonRegistration: HTMLButtonElement = document.querySelector('.header__btn_register');
  const buttonStartGame: HTMLButtonElement = document.querySelector('.header__btn_start');
  const cover: HTMLElement = document.querySelector('.rules__cover');
  const formOfRegistration: HTMLElement = document.querySelector('.popup');

  if (buttonRegistration) {
    buttonRegistration.addEventListener('click', (): void => {
      showWinPopupAndHisCover(formOfRegistration, cover);
    });
  }

  if (buttonStartGame) {
    buttonStartGame.addEventListener('click', () => {
      store.state.isGameStarted = true;
      window.location.hash = '#/game';
      store.callSubscriber();
    });
  }
};

export default HeaderLogic;
