import { addUserToDataBase } from './database';

interface Store {
  state: State,
  addUserToDataBase: Function,
  callSubscriber: Function,
  subscribe: Function,
}


interface State {
  isUserRegistered: boolean,
  isGameStarted: boolean,
  newUser: object,
  gamePage: GamePage,
}

interface GamePage {
  timeInSeconds: number,
  numberOfAllComparisons: number,
  numberOfWrongComparisons: number,
  playerPoints: number,
  difficult: string,
  cardsType: string,
}
const store: Store = {
  state: {
    isUserRegistered: false,
    isGameStarted: false,
    newUser: {},
    gamePage: {
      timeInSeconds: 0,
      numberOfAllComparisons: 0,
      numberOfWrongComparisons: 0,
      playerPoints: 0,
      difficult: 'beginner',
      cardsType: 'animals',
    },
  },
  addUserToDataBase: addUserToDataBase,
  callSubscriber() {},
  subscribe(observer: Function): void {
    this.callSubscriber = observer;
  },
};

export default store;
