function addToStateWhatGameFinished() {
  store.state.isGameStarted = false;
}

function goToScorePage() {
  window.location.hash = '#/score';
}
function addPlayerPointsToState(playerPoints: number): void {
  store.state.newUser.score = playerPoints;
}
function hideWinPopupAndHisCover(winPopup: HTMLElement, coverWInPopup: HTMLElement): void {
  document.body.classList.remove('notScrollable');
  winPopup.classList.remove('appear');
  coverWInPopup.classList.remove('appear');
}
function showWinPopupAndHisCover(winPopup: HTMLElement, coverWInPopup: HTMLElement): void {
  document.body.classList.add('notScrollable');
  winPopup.classList.add('appear');
  coverWInPopup.classList.add('appear');
}
function addTextInWinPopup(time: number, playerPoints: number): void {
  const seconds: number = time % 60;
  const minutes: number = Math.floor(time / 60);
  const winPopupText: HTMLElement = document.querySelector('.win-popup__text');
  winPopupText.innerHTML = `Congratulations! You successfully found all matches 
    on ${minutes}.${seconds} minutes. Your points: ${playerPoints}`;
}

function addCongratulationText(playerPoints: number, gameTime: number) {
  const cover: HTMLElement = document.querySelector('.game__win-popup-cover');
  const winPopup: HTMLElement = document.querySelector('.win-popup');
  const winPopupButton: HTMLElement = document.querySelector('.win-popup__btn');

  addTextInWinPopup(gameTime, playerPoints);
  showWinPopupAndHisCover(winPopup, cover);
  cover.addEventListener('click', (): void => {
    hideWinPopupAndHisCover(winPopup, cover);
  });
  winPopupButton.addEventListener('click', (): void => {
    hideWinPopupAndHisCover(winPopup, cover);
    addToStateWhatGameFinished();
    goToScorePage();
  });
}

function countPlayerPoints(): number {
  const gameTime: number = store.state.gamePage.timeInSeconds;
  const { numberOfAllComparisons } = store.state.gamePage;
  const { numberOfWrongComparisons } = store.state.gamePage;
  let playerPoints: number = (numberOfAllComparisons - numberOfWrongComparisons) * 100 - gameTime * 10;
  if (playerPoints < 0) {
    playerPoints = 0;
  }
  return playerPoints;
}

function finishGame(timerId): void {
  clearInterval(timerId);
  const playerPoints: number = countPlayerPoints();
  const gameTime: number = store.state.gamePage.timeInSeconds;
  addCongratulationText(playerPoints, gameTime);
  addPlayerPointsToState(playerPoints);
}

function timer(passedTime = 0): number {
  let seconds = 0;
  let minutes = 0;
  let timeInSeconds: number = passedTime;
  const minutesField: HTMLElement = document.querySelector('.game__minutes');
  const secondsField: HTMLElement = document.querySelector('.game__seconds');
  const timerId: number = setInterval(() => {
    timeInSeconds++;
    seconds = timeInSeconds % 60;
    minutes = Math.floor(timeInSeconds / 60);
    minutesField.innerHTML = minutes < 10 ? `0${String(minutes)}` : String(minutes);
    secondsField.innerHTML = seconds < 10 ? `0${String(seconds)}` : String(seconds);
    store.state.gamePage.timeInSeconds = timeInSeconds;
  }, 1000);
  return timerId;
}

function isAllCardsGuessed(cards): boolean {
  const whichElementsGuessed: boolean[] = [];
  cards.forEach((card: HTMLElement) => {
    whichElementsGuessed.push(card.classList.contains('guessed'));
  });
  if (whichElementsGuessed.every((elem) => elem)) {
    return true;
  }
  return false;
}

function startPartOfGuessing(timerIdOfMemorizingPart) {
  clearInterval(timerIdOfMemorizingPart);
  const cards = document.querySelectorAll('.game__card');
  cards.forEach((card) => card.classList.remove('flipped'));
  let timerIdOfGuessingPart: number = timer();
  const buttonStop = document.querySelector('.header__btn_stop');
  buttonStop.addEventListener('click', () => {
    const coverOverCardsField = document.querySelector('.game__cards-cover');
    if (buttonStop.classList.contains('stopped')) {
      timerIdOfGuessingPart = timer(store.state.gamePage.timeInSeconds);
      buttonStop.classList.remove('stopped');
      coverOverCardsField.classList.remove('appear');
      buttonStop.innerHTML = 'Stop game';
    } else {
      buttonStop.classList.add('stopped');
      clearInterval(timerIdOfGuessingPart);
      coverOverCardsField.classList.add('appear');
      buttonStop.innerHTML = 'Resume game';
    }
  });
  cards.forEach((card: Element) =>
    card.addEventListener('click', () => {
      let cardFlipped: Element;
      cards.forEach((c) => {
        if (c.classList.contains('flipped') && !c.classList.contains('guessed')) {
          cardFlipped = c;
        }
      });
      card.classList.add('flipped');
      if (cardFlipped) {
        if (!(card.id === cardFlipped.id)) {
        const cardFlippedImg: HTMLImageElement = cardFlipped.querySelector('.game__back-pic');
        const cardFlippedImgUrl: string = cardFlippedImg.src;
        const cardCurrentImg: HTMLImageElement = card.querySelector('.game__back-pic');
        const cardCurrentImgUrl: string = cardCurrentImg.src;
        const coverCardCurrent: HTMLElement = card.querySelector('.game__cover');
        const coverCardFlipped: HTMLElement = cardFlipped.querySelector('.game__cover');

        if (cardFlippedImgUrl === cardCurrentImgUrl) {
          store.state.gamePage.numberOfAllComparisons++;
          coverCardCurrent.classList.add('game__cover_correct');
          coverCardFlipped.classList.add('game__cover_correct');
          card.classList.add('guessed');
          cardFlipped.classList.add('guessed');
          if (isAllCardsGuessed(cards)) {
            setTimeout((): void => finishGame(timerIdOfGuessingPart), 400);
          }
        } else {
          store.state.gamePage.numberOfAllComparisons++;
          store.state.gamePage.numberOfWrongComparisons++;
          coverCardCurrent.classList.add('game__cover_wrong');
          coverCardFlipped.classList.add('game__cover_wrong');
          setTimeout((): void => {
            card.classList.remove('flipped');
            cardFlipped.classList.remove('flipped');
            coverCardCurrent.classList.remove('game__cover_wrong');
            coverCardFlipped.classList.remove('game__cover_wrong');
          }, 600);
        }
      }
      }
    })
  );
}

function changeCardsFieldStylesAccordingToLevel(): void {
  const cardsField: HTMLElement = document.querySelector('.game__cards');
  if (store.state.gamePage.difficult === 'beginner') {
    cardsField.classList.add('game__cards_beginner-level');
  } else if (store.state.gamePage.difficult === 'medium') {
    cardsField.classList.add('game__cards_medium-level');
  }
}

const GameLogic = (): void => {
  const timeOfMemorizingPartInMilliseconds = 30000;
  changeCardsFieldStylesAccordingToLevel();
  if (store.state.isGameStarted) {
    const cards = document.querySelectorAll('.game__card');
    let cardsImages8;
    let cardsImages18;
    if (store.state.gamePage.cardsType === 'animals') {
      cardsImages8 = cardsAnimalsImages8;
      cardsImages18 = cardsAnimalsImages18;
    } else if (store.state.gamePage.cardsType === 'programming') {
      cardsImages8 = cardsProgrammingImages8;
      cardsImages18 = cardsProgrammingImages18;
    }
    const cardsImages16 = cardsImages8.concat(cardsImages8);
    const cardsImages36 = cardsImages18.concat(cardsImages18);
    cards.forEach((card) => {
      const cardsImages = store.state.gamePage.difficult === 'beginner' ? cardsImages16 : cardsImages36;
      const randomNumber: number = Math.floor(Math.random() * cardsImages.length);
      const cardImg: HTMLImageElement = card.querySelector('.game__back-pic');
      cardImg.src = cardsImages[randomNumber];
      cardsImages.splice(randomNumber, 1);
    });

    const timerIdOfMemorizingPart: number = timer();
    cards.forEach((card) => card.classList.add('flipped'));
    setTimeout(() => startPartOfGuessing(timerIdOfMemorizingPart), timeOfMemorizingPartInMilliseconds);
  }
};

export default GameLogic;
