import App from './app';
import GameLogic from './Logic/GameLogic/GameLogic';
import HeaderLogic from './Logic/HeaderLogic/HeaderLogic';
import PopupLogic from './Logic/PopupLogic/PopupLogic';
import SettingsLogic from './Logic/SettingsLogic/SettingsLogic';
import store from './redux/store';
import './sass/style.scss';

window.onload = (): void => {
  function renderAll(): void {
    const currentPath: string = window.location.hash.slice(1).toLowerCase() || '/';
    document.body.innerHTML = App({ state: store.state, path: currentPath });
    HeaderLogic();
    PopupLogic();
    if (currentPath === '/game') {
      GameLogic();
    } else if (currentPath === '/settings') {
      SettingsLogic();
    }
  }
  renderAll();
  store.subscribe(renderAll);
  window.addEventListener('hashchange', (): void => renderAll());
};
