import store from '../../../redux/store';

const CardTypesSettingsLogic = (): void => {
  const cardsTypeSettings: HTMLElement = document.querySelector('.cardsType');
  const cardsTypeSettingsBlockList: HTMLUListElement = cardsTypeSettings.querySelector('.settings__types');
  const cardsTypeSettingsTypes = cardsTypeSettings.querySelectorAll('.settings__type');
  cardsTypeSettings.addEventListener('click', () => {
    cardsTypeSettingsBlockList.classList.add('appear');
  });
  cardsTypeSettingsTypes.forEach((type) => {
    type.addEventListener('click', () => {
      if (type.classList.contains('settings__type_animals')) {
        store.state.gamePage.cardsType = 'animals';
      } else if (type.classList.contains('settings__type_programming')) {
        store.state.gamePage.cardsType = 'programming';
      }
    });
  });
};

export default CardTypesSettingsLogic;
